:root {
  --blue-bondi-ocean: rgba(67, 93, 137, 1);
  --blue-bondi-sky: rgba(83, 133, 197, 1);
  --blue-bondi-sky2: rgba(70, 116, 185, 1);
  --blue-bondi-sky-edge: rgba(63, 106, 176, 1);
  --white-bondi-cloud: rgba(225, 226, 224, 1);
  --white-bondi-cloud-60: rgba(225, 226, 224, 0.6);
  --sand-tama-light: rgba(210, 193, 170, 1);
  --sand-tama-heavy: rgba(194, 179, 160, 1);
  --blue-tama-ocean: rgba(59, 108, 129, 1);
  --blue-tama-ocean-20: rgba(59, 108, 129, 0.2);
  --blue-tama-ocean-40: rgba(59, 108, 129, 0.4);
  --blue-tama-ocean2: rgba(13, 25, 53, 1);
  --crimson-liquids-contrast: rgba(212, 109, 106, 1);
}
.header {
  background: var(--blue-bondi-sky2);
  height: 200px;
}

.header h1 {
  margin: 0;
  padding-top: 75px;
  text-align: center;
  color: var(--sand-tama-light);
  font-size: 3rem;
}

.footer {
  background: var(--blue-tama-ocean2);
  height: 200px;
}

.footer h2 {
  margin: 0;
  padding-top: 75px;
  text-align: center;
  color: var(--sand-tama-heavy);
  font-size: 3rem;
}

/* Home/index.js */

.home {
  margin: 20px;
}

/* Centered text */
.blurb {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: var(--blue-bondi-ocean);
}

.section {
  /* background: #f3e5f5;
  padding: 20px; */
  display: flex;   /* children to use flex */
  background-color: var(--white-bondi-cloud);
}

.section h2 {
  text-align: center;
}

.flexbox-container {
  flex-direction: column;
}

.flexbox-item {
  margin: 10px;
  min-height: 100px;
  display: flex;
}

.flexbox-item h3 {
  flex-grow: 0;       /* do not grow */
  flex-shrink: 0;       /* do not shrink */
  flex-basis: 100px;
  padding: 10px;
  margin: 10px;
  width: 100px;
  text-align: right;
  border-right: 2px solid var(--blue-bondi-ocean);
}

.flexbox-item p {
  padding: 10px;
  margin: 10px;
  text-align: left;
}

/* PROJECT CARDS START */

#article-project-work {
  margin: 10px;
  min-height: 100px;
  display: flex;
}

.projects {
  flex: 4 1;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.section-project-card {
  border-color: var(--blue-bondi-sky);
  border-style: solid;
  border-width: 1px;
  border-radius: 10px;
  padding: 10px;
  flex: 1 1 auto;       /* grow shrink*/
}

.section-project-card header {
  color: var(--white-bondi-cloud);
  padding: 10px;
}

.section-project-card p {
  padding: 0;
  margin: 0;
  text-align: center;
  font-size: x-small;
}

.section-project-card a {
  font-size: x-small;
}

.section-project-card img {
  width: 200px;
  height: 200px;
  /* flex-basis: 100px; */
  justify-content: space-around;
  align-items: stretch;
}

#first-project img {
  /* flex: 1 1 300px; */
  justify-content: space-around;
  /* align-items: stretch; */
  /* width: 100%;
  height: 100%; */
  object-fit: fill;
}

.form {
  margin: 20px;
}

.form input {
  display: block;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 100%;
}

